import "./styles/style.scss";

import edit from "./components/edit";
import save from "./components/save";
import { register } from "../../helpers";
import svg from "../../helpers/svg";
import metadata from "./block.json";

register(metadata.name, {
	icon: svg.section,
	edit,
	save,
});

import { __ } from "@wordpress/i18n";
import Importer from "../../components/importer";
import { select } from "@wordpress/data";
import {
	PluginBlockSettingsMenuItem,
	PluginPostStatusInfo,
} from "@wordpress/edit-post";
import { registerPlugin } from "@wordpress/plugins";
import { exportToJsonFile, insertableBlocks } from "../../helpers";

if (typeof wp.editPost != "undefined") {
	registerPlugin("rise-blocks-plugin-sidebar", {
		render: () => (
			<>
				<PluginBlockSettingsMenuItem
					allowedBlocks={insertableBlocks}
					icon="download"
					label={__("Export To JSON", "rise-blocks")}
					onClick={(e) => {
						const clientId = select("editor").getSelectedBlockClientId();
						exportToJsonFile(clientId);
					}}
				/>
				<PluginPostStatusInfo>
					<Importer />
				</PluginPostStatusInfo>
			</>
		),
	});
}
